html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

* {
    margin: 0;
}

html {
    height: 100%;
}

body {
    background-image: url(logo.svg), -webkit-linear-gradient(top left, #162c1f 0%, #007d35 40%, #0f574e 100%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: flex-end;
    height: 100%;
    width: 100%;
}

@media (min-width:768px) {
    body {
        align-items: center;
    }
}

#root {
    width: 100%;
    height: 100%;
}

.contenedor {
    display: flex;
    align-items: center;
    padding-top: 10rem;
    flex-direction: column;
}

.frase {
    padding: 3rem;
    border-radius: 1em;
    background-color: rgba(255, 255, 255, 0.705);
    max-width: 50em;
    margin: 0 2em;
}

.frase h1::before {
    content: open-quote;
    font-size: 10rem;
    color: black;
    position: absolute;
    left: -1rem;
    top: -2rem;
}

.frase h1 {
    font-family: 'Staatliches', cursive;
    text-align: center;
    position: relative;
    padding-left: 4rem;
}

.frase p {
    font-family: 'Handlee', cursive;
    font-size: 1.6rem;
    color: #666;
    text-align: right;
    margin-top: 2rem;
}

button {
    background: #000;
    font-family: 'Staatliches', cursive;
    text-transform: capitalize;
    border-radius: .7em;
    color: white;
    margin-top: 3rem;
    border: none;
    outline: none;
    padding: 1em 2em;
}